<script>
import axios from 'axios';
import '@/assets/css/edit/edit.css'
import ManagerFooter from "@/components/ManagerFooter.vue"
import ManagerHeader from "@/components/ManagerHeader.vue"
import TopButton from "@/components/TopButton.vue"
import ToReserve from "@/components/ToReserve.vue"
import { Carousel, Pagination, Slide } from "vue3-carousel";
import "vue3-carousel/dist/carousel.css";
import { KakaoMap, KakaoMapMarker } from 'vue3-kakao-maps';
// import { ref  } from "vue";


export default {
    name: "EditView",
    components: {
        ManagerFooter,
        ManagerHeader,
        TopButton,
        Carousel,
        Slide,
        Pagination,
        // Navigation,
        ToReserve,
        KakaoMap,          // 
        KakaoMapMarker     // 
    },
    data() {
        return {
            bNo: this.$route.params.bNo,
            shopInfo: {
                bZipCode: "",
                bAddress: "",
                bdAddress: "",
                bPhone: "",
                title: "",
                subTitle: "",
                logo:"",
                dName:"",
                introduce:"",
                dProfile:"",
                job:"",
            },
            priceList:[],
            slideList:[],
            cutList:[],
            slides: [], //  
            slides2: [], //   
            carouselRef: null,
            carouselRef2: null,
            coordinate: {
                lat: 0,
                lng: 0,
            },
        }
    },
    computed: {
        chunkedSlides() {
            return this.chunk(this.slides2, 18);
        },
    },  
    setup() { //props, context     , reactive, ref      ,       
    },
    methods: {
        chunk(array, size) {
            return array.reduce((acc, _, i) => {
                if (i % size === 0) {
                acc.push(array.slice(i, i + size));
                }
                return acc;
            }, []);
        },

        getLatLngSlide() {
            axios({
                method: 'get',
                url: `${this.$store.state.apiBaseUrl}/api/su/shopInfo`,
                headers: { 'Content-Type': 'application/json; charset=utf-8' },
                params: { bNo: this.bNo },
                responseType: 'json',
            })
                .then((response) => {
                const data = response.data.apiData;
                this.coordinate.lat = data.shopInfo.latitude;
                this.coordinate.lng = data.shopInfo.longitude;
                this.slides = data.sList;
                this.slides2 = data.cList;
                })
                .catch((error) => {
                console.log(error);
                });
            },
            nextSlide() {
            if (this.carouselRef && this.carouselRef.next) {
                this.carouselRef.next();
            }
            },
            prevSlide() {
            if (this.carouselRef && this.carouselRef.prev) {
                this.carouselRef.prev();
            }
        },

        //   
        getShopInfo(){
            // console.log(this.bNo);
            console.log("getShopInfo? ")
            console.log(this.$store.state.auth.bNo);
            console.log(this.$store.state.auth);
            console.log(this.bNo);
            console.log(this.$route.params.bNo);
            axios({
                method: 'get', // put, post, delete 
                url: `${this.$store.state.apiBaseUrl}/api/su/shopInfo`,
                headers: { "Content-Type": "application/json; charset=utf-8" }, //
                params: {bNo: this.bNo}, //get   
                // data: this.userVo, //put, post, delete   JSON  
                responseType: 'json' //
            }).then(response => {

                console.log(response);
                this.shopInfo = response.data.apiData.shopInfo;
                this.priceList = response.data.apiData.pList;
                // this.slideList = response.data.apiData.sList;

                // console.log(this.shopInfo.logo);
                // console.log(this.shopInfo.dProfile);
                // console.log(this.priceList);
                // console.log(this.slideList);

            }).catch(error => {
                console.log(error);
            });
        },
    },
    created(){
        this.getShopInfo();
        this.getLatLngSlide();
    }
}
</script>
